.category-preview-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;

  &-title{
    font-size: 28px;
    margin-bottom: 30px;
    color:black;
    text-align: center;
    cursor: pointer;
  }

  &-preview{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 20px;
  }

  &-footerTitle{
    font-size: 24px;
    color:#ccc;
    text-align: center;
    margin: 30px;
  }
}