@mixin shrinkLabel {
  top: -14px;
  font-size: 12px;
  color: black;
}

.form-input {
  position: relative;
  margin: 45px 0;

  &__input {
    background: none;
    background-color: white;
    color: black;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid grey;
    margin: 25px 0;

    &:focus {
      outline: none;
    }

    &:focus ~ label {
      @include shrinkLabel;
    }
  }

  input[type='password'] {
    letter-spacing: 0.3em;
  }

  &__label {
    color: grey;
    opacity: .7;
    font-size: 16px;
    font-weight: normal;
    pointer-events: none;
    @include position-absolute(10px,5px,0,0);
    transition: 300ms ease all;

    &.shrink {
      @include shrinkLabel;
    }
  }
}