.checkoutItem-container{
  width: 100%;
  @include d-flex(space-between);
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 15px 10px;
  font-size: 18px;

  &-image{
    width: 23%;
    padding-right: 15px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &-name,
  &-quantity,
  &-price {
    width: 20%;
  }

  &-quantity {
    padding-left: 15px;
    display: flex;
    &__arrow {
      cursor: pointer;
    }
    &__value {
      margin: 0 10px;
    }
  }

  &-removebtn {
    width: 5%;
    padding-left: 6px;
    cursor: pointer;
  }
}